import React from 'react';
import p0 from './0.jpg';
import p1 from './1.jpg';
import p2 from './2.jpg';
import p3 from './3.jpg';
import p4 from './4.jpg';
import p5 from './5.jpg';
import p6 from './6.jpg';
import p7 from './7.jpg';
import p8 from './8.jpg';
import './App.css';
import {Container, Row, Col, Button} from 'react-bootstrap';

const defaultOrder = [p0, p1, p2, p3, p4, p5, p6, p7, p8];

export default class Puzzle extends React.Component {
    constructor(props) {
        super(props);
        this.shuffle = this.shuffle.bind(this);
        this.swap = this.swap.bind(this);
        this.arraysEqual = this.arraysEqual.bind(this);
        this.reset = this.reset.bind(this);
        this.state = {
            images: this.shuffle(defaultOrder),
            checked: [false, false, false, false, false, false, false, false, false],
            numChecked: 0,
            selected: [],
            turns: 0
        }
    }

    shuffle(a) {
        for (let i = a.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [a[i], a[j]] = [a[j], a[i]];
        }
        return a;
    }

    componentDidMount() {
        this.setState({
            images: this.shuffle(defaultOrder)
        })
    }

    toggleCheck(index) {
        let temp = this.state.checked.slice();
        let selected = this.state.selected.slice();
        if (this.state.numChecked == 2 && !temp[index]) return;
        if (temp[index]) selected.splice(selected.indexOf(index), 1);
        else selected.push(index);
        let tempChecked = temp[index] ? this.state.numChecked - 1 : this.state.numChecked + 1;
        temp[index] = !temp[index];
        this.setState({
            checked: temp,
            numChecked: tempChecked,
            selected: selected
        }, () => {
        })
    }

    reset() {
        this.setState({
            images: this.shuffle([p0, p1, p2, p3, p4, p5, p6, p7, p8]),
            checked: [false, false, false, false, false, false, false, false, false],
            numChecked: 0,
            selected: [],
            turns: 0
        })
    }

    swap() {
        let temp = this.state.numChecked;
        if (temp != 2) {
            alert("You must select two pieces to swap!");
            return;
        }
        let turns = this.state.turns + 1;
        let images = this.state.images.slice();
        let a = this.state.selected[0];
        let b = this.state.selected[1];
        let t = images[a];

        images[a] = images[b];
        images[b] = t;

        if (this.arraysEqual(images, [p0, p1, p2, p3, p4, p5, p6, p7, p8])) {
            this.setState({
                images: images,
                checked: [false, false, false, false, false, false, false, false, false],
                numChecked: 0,
                selected: [],
                turns: turns
            }, () => {
                setTimeout(() => {
                    alert("You solved the puzzle with " + turns + " swaps! The puzzle will now reset.");
                    this.reset();
                }, 500);
            })
        } else {
            this.setState({
                images: images,
                checked: [false, false, false, false, false, false, false, false, false],
                numChecked: 0,
                selected: [],
                turns: turns
            }, () => {

            })
        }
    }

    arraysEqual(a, b) {
        if (a === b) return true;
        if (a == null || b == null) return false;
        if (a.length !== b.length) return false;

        for (let i = 0; i < a.length; ++i) {
            if (a[i] !== b[i]) return false;
        }
        return true;
    }

    render() {
        return (
            <Container fluid>
                <Row>
                    <Col>
                        <table>
                            <tr>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[0]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[0]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(0)}/>
                                    </div>
                                </td>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[1]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[1]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(1)}/>
                                    </div>
                                </td>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[2]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[2]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(2)}/>
                                    </div>
                                </td>
                                <td>
                                    <div className={"card-2 countlabel"} style={{cursor: "default"}}>Swaps Done: {this.state.turns}</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[3]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[3]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(3)}/>
                                    </div>
                                </td>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[4]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[4]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(4)}/>
                                    </div>
                                </td>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[5]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[5]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(5)}/>
                                    </div>
                                </td>
                                <td>
                                    <div>
                                        <button onClick={this.swap} className={"card-1 button"}>Perform Swap</button>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[6]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[6]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(6)}/>
                                    </div>
                                </td>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[7]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[7]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(7)}/>
                                    </div>
                                </td>
                                <td>
                                    <div className="square card-1" style={{backgroundImage: `url(${this.state.images[8]}`, backgroundColor: "red"}}>
                                        <input checked={this.state.checked[8]} className="checkbox" type={"checkbox"} onClick={() => this.toggleCheck(8)}/>
                                    </div>
                                </td>
                            </tr>
                        </table>
                    </Col>
                </Row>
            </Container>

        )
    }
}